@font-face
{
font-family: ogg;
src: url('fonts/Ogg-Roman.woff2');
}

html, body{
  font-size: 10px;
}

.App {
  text-align: center;
  font-family: "Sailec-Regular", "Segoe UI", Roboto, "Helvetica Neue", Helvetica, Arial, sans-serif;
  max-width: 720px;
  margin: 0 auto;
  min-height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.normal-font{
  font-family: "Sailec-Regular", "Segoe UI", Roboto, "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: normal;
}

.theme-font{
  font-family: Ogg, Garamond, "Times New Roman", serif;
}

.App-links{
  padding-top: 2px;
}

.home-link a{
  display: block;
  font-size: 18px;
  height: 50px;
  line-height: 50px;
  padding-left: 20px;
  color: #fff;
  background-color: #060606;
  text-decoration: none;
  text-align: left;
  margin-top: 1px;
}

.home-link a:active{
  background-color: #111;
}

.home-link a i{
  float: right;
  height: 50px;
  line-height: 50px;
  padding-right: 20px;
}


.App-header {
  background-color: #FFF;
  height: 10rem;
  color: #222;
}

.App-footer{
  height: 40px;
  line-height: 40px;
  color: #222;
  font-size: 12px;
}


.App-intro {
  -ms-flex: 1 1;
      flex: 1 1;
  font-size: large;
  background-color: #000;
  color: #FFF;
}



