.single-rank-title{
  font-size: 18px;
  height: 50px;
  line-height: 50px;
  padding-left: 20px;
  color: #fff;
  background-color: #060606;
  text-decoration: none;
  text-align: left;
  margin-top: 1px;
}

.single-rank-title i{
    font-size: 12px;
    font-style: normal;
}

.single-rank-item{
    display: -ms-flexbox;
    display: flex;
    padding: 10px;
    background: #121212;
    -ms-flex-align: center;
        align-items: center;
}

.single-rank-item .name{
    -ms-flex: 1 1;
        flex: 1 1;
    text-align: left;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: baseline;
        align-items: baseline;
}

.single-rank-item .title{
    padding-left: 20px;
}
.single-rank-item .desc{
    padding-left: 10px;
    font-size: 12px;
}

.single-rank-item .rank{
    margin-right: 20px;
    width: 32px;
    height: 32px;
    font-size: 14px;
    border-radius: 20px;
    line-height: 32px;
    background: #333;
    border: 4px solid #999
}



